@import url('https://fonts.googleapis.com/css?family=Black+Han+Sans:400');
@import url('https://fonts.googleapis.com/css?family=Black+Han+Sans&display=swap&subset=korean');
html, body {
    width: 100%;
    overflow-x: hidden;
}
/* -------------------------- LeeJungHoon 로고 스타일 -------------------------- */
.logo {
    font-size: 3.5rem;
    font-weight: 700;
    cursor: default;
    font-family: Allura;
    -webkit-background-clip: text;
    background-clip: text;
    color: white;

}

.logo:hover {
    cursor: pointer;
}

.navbarcontainer {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    position: fixed;
    z-index: 100;
}

.navbar_main_flex_box {
    top: 0;
    max-width: 1400px;
    padding: 20px;
    height: 80px;
    width: 100%;
    justify-content: space-between;
}

/* -------------------------- navbar 오른쪽 버튼들 스타일 -------------------------- */
.navbarButton {
    font-family: Aladin;
    font-size: 1.5rem;
    color: white;
}

/* -------------------------- homeBody -------------------------- */

/* -------------------------- intro 관련 스타일 -------------------------- */
.intro_Container {
    background-image: url('../img/mainImg.jpg');
    width: 100%;
    height: 100vh;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    flex-direction: column; /* 자식 요소들을 세로로 정렬 */
    justify-content: center; /* 중앙 정렬 */
    align-items: center; /* 가로 중앙 정렬 */
}

.introContainer2 {
    font-family: 'GmarketSansMedium';
    color: white;
    flex-direction: column;
    font-weight: bold;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    text-align: center;
}

.introContainer3 {
    font-size: 3.5rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.introContainer3 > Text {
    margin-right: 0.5rem; /* 옆 텍스트와의 간격 조절 */
}

.introContent {
    font-size: 1.3rem;
}

.introContent2 {
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.intro_main_text1 {
    margin-top: 56px;
    font-size: 2.7rem;
}

.intro_main_text2 {
    font-size: 2rem;
}
.highlighted-name {
    background: linear-gradient(to right, gray, white, gray); /* 여기서 색상을 원하는 그라데이션으로 조정하세요 */
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    margin-right: 10px;
    font-size: 3rem;
}

.highlighted-self {
    background: linear-gradient(to right, gray, white, gray); /* 여기서 색상을 원하는 그라데이션으로 조정하세요 */
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.intro_main_text3 {
    margin-top: 20px;
    color: white;
    padding: 20px;
    font-size: 1rem;
    font-weight: 300;
    text-align: start;
}

/* -------------------------- 이력서 다운로드 버튼 스타일 -------------------------- */
.resumebtn {
    width: 220px;
    margin-top: 20px;
    padding: 0.5em 1.5rem;
    font-size: 1.2rem;
    letter-spacing: .1rem;
    font-weight: 600;
    border: 1px solid gray;
    border-radius: 20px;
}

.resumetext {
    width: 100%;
}

.resumebtn:hover {
    background: gray;
    color: black;
}

/* -------------------------- 이력서 다운로드 화살표 관련 스타일 -------------------------- */
.mb-arrow {
    display: inline-block; /* 또는 원하는 display 타입 */
    animation: bounce 1s infinite; /* 1초마다 무한 반복 */
    font-size: 3rem;
}
.mb-arrow2 {
    background: linear-gradient(to right, red, white, gray);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    color: transparent; /* 크로스 브라우징을 위해 추가 */
}

@keyframes bounce {
    0%, 100% {
        transform: translateY(-5px); /* 위로 10px 움직입니다. */
    }
    50% {
        transform: translateY(5px); /* 아래로 10px 움직입니다. */
    }
}

/* ------------- about두번째단 ------------- */
/* -------------------------- aboutMe 관련 스타일 -------------------------- */

.blank {
    width: 40px;
    height: 40px;
    opacity: 0.5;
    margin-bottom: 10px;
}

.blank:hover {
    cursor: pointer;
    color: black;
}

.blank2 {
    width: 40px;
    height: 40px;
    color: darkgray;
    opacity: 0.5;
    margin-bottom: 10px;
}

.blank2:hover {
    cursor: pointer;
    color: white;
}


.about_me_Container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 40px;
}

.about-me-item {
    display: flex;
    margin-top: 20px;
}

.about_Me {
    font-family: 'TTHakgyoansimJiugaeR';
    text-align: center;
    font-weight: 700;
    color: #000000;
    font-size: 3rem;
}

.about_me2 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 50px;
    font-size: 1rem;
    font-weight: bold;
    color: #333333;
    font-family: 'GmarketSansMedium';
    max-width: 700px;
}

.icon_aboutme {
    font-size: 2rem;
    margin-top: 2px;
}


.content_aboutme {
    font-size: 1.25rem;
}

.aboutUnderBar {
    width: 300px;
    border: 1px solid #eeeeee;
    margin-left: 4rem;
}

.about_me_image {
    display: flex;
    justify-content: center;
    align-items: center;
}

.about_me_main_text {
    width: 700px;
    margin-top: 40px;
    font-weight: 500;
    color: black;
}

.about_me_text1 {
    background: linear-gradient(40deg, gray, black, gray);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-weight: 700;
    border: 1px solid gray;
    text-align: center;
    padding: 10px;
    border-radius: 20px;
    box-shadow: 3px 3px 3px 3px gray;
}

.github_link:hover {
    color: black;
}

/* ------------- skills (세번째단) ------------- */
.skilscontainer {
    background: #8A7E72;
    margin-top: 50px;
    padding: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.skills-cards-container {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-around;
}

.skills_me {
    font-family: 'TTHakgyoansimJiugaeR';
    font-size: 3rem;
    font-weight: 700;
    color: white;
}

.skill-logo-box {
    position: relative;
    border-radius: 20px;
    overflow: hidden;
}

.skill-logo-layer {
    position: absolute;
    bottom: -100%; /* 초기 상태에서는 박스 바깥에 위치하도록 설정 */
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5); /* 반투명 검정색 배경 */
    display: flex;
    justify-content: center;
    align-items: center;
    transition: bottom 0.3s; /* 부드러운 효과 */
    font-family: 'TTHakgyoansimJiugaeR';
}

.skill-logo-box:hover .skill-logo-layer {
    bottom: 0; /* 마우스 오버 시 박스 내부 바닥으로 이동 */
}

.skill-logos {
    transition: transform 0.5s ease-in-out; /* 변형에 대한 부드러운 전환 효과 적용 */
    border-radius: 20px;
    /*width: 150px;*/
    width: 8rem;
}

.skill-logos2 {
    transition: transform 0.5s ease-in-out; /* 변형에 대한 부드러운 전환 효과 적용 */
    border-radius: 20px;
    /*width: 120px;*/
}

.skill-logo-box:hover .skill-logos  {
    transform: scale(0.9);
}
.skill-logo-box:hover .skill-logos2 {
    transform: scale(0.9);
}

.skillsTypeBox {
    width: 400px;
    height: 500px;
    border-radius: 60px;
    background: white;
}

.skillstype {
    display: flex;
    justify-content: start;
    align-items: center;
    font-size: 2rem;
    flex-direction: column;
    margin-top: 20px;
}

.skillstext {
    font-family: 'TTHakgyoansimJiugaeR';
    font-weight: 500;
    margin-bottom: 12px;
}

.skillsBox {
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.skillsBox2 {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 80%;
}

/* ------------- repository (네번째단) ------------- */
.repositoryBox {
    height: 100%;
    padding: 80px;
    background: #222222;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.repository_me {
    font-family: 'TTHakgyoansimJiugaeR';
    font-size: 3rem;
    font-weight: 700;
    color: white;
}

.repositoryContainer {
    font-family: 'GmarketSansMedium';
    font-weight: 400;
    color: #333333;
    gap: 40px;
}

.repositoryGithub, .repositoryBlog {
    width: 400px;
    height: 560px;
    background: white;
    border-radius: 60px;
    margin-top: 50px;
    padding: 10px;
    transition: transform 0.5s; /* 변형에 대한 부드러운 전환 효과 적용 */
}

.repositoryGithub:hover {
    transform: scale(1.1);
}

.repositoryBlog:hover {
    transform: scale(1.1);
}

.repositoryImgBox {
    display: flex;
    justify-content: center;
    align-items: center;
}

/* ------------- project (다섯번째단) ------------- */
.projectContainer {
    height: 100%;
    padding: 80px;
    background: linear-gradient(50deg,#1C646D,white, #1C646D);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.projectsortbox {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.project_me {
    font-family: 'TTHakgyoansimJiugaeR';
    font-size: 3rem;
    font-weight: 700;
    color: white;
}

.projectMain {
    margin-top: 40px;
    width: 1100px;
    height: 100%;
    background: #eeeeee;
    border-radius: 60px;
    padding: 40px;
    -webkit-box-shadow: 0 19px 38px rgba(0, 0, 0, 0.30), 0 15px 12px rgba(0, 0, 0, 0.22);
    -moz-box-shadow: 0 19px 38px rgba(0, 0, 0, 0.30), 0 15px 12px rgba(0, 0, 0, 0.22);
    -ms-box-shadow: 0 19px 38px rgba(0, 0, 0, 0.30), 0 15px 12px rgba(0, 0, 0, 0.22);
    -o-box-shadow: 0 19px 38px rgba(0, 0, 0, 0.30), 0 15px 12px rgba(0, 0, 0, 0.22);
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.30), 0 10px 10px rgba(0, 0, 0, 0.22);
}

.projectImg {
    width: 50%;
}

.primaryProjectContent {
    font-family: 'Black Han Sans';
    font-size: 3rem;
    font-weight: 400;
}
.secondaryProjectContent {
    font-family: 'Black Han Sans';
    font-size: 3rem;
    font-weight: 400;
}
.toyProjectContent {
    font-family: 'Black Han Sans';
    font-size: 3rem;
    font-weight: 400;
}
.portfolioProjectContent {
    font-family: 'Black Han Sans';
    font-size: 3rem;
    font-weight: 400;
}

.projectContent {
    font-family: 'Pretendard-Regular';
    font-weight: 400;
}

.projectContentButton {
    font-family: 'Pretendard-Regular';
    font-weight: 400;
    margin-top: 20px;
    background: black;
    color: white;
    padding: 7px;
    border-radius: 10px;
}

.projectContentButton:hover {
    background: gray;
    color: white;
}

.projectURLlink {
    color: blue;
}

.projectURLlink:hover {
    text-decoration: underline;
}

.projectName {
    font-family: Allura;
    font-size: 3rem;
}

.project_modal_body {
    font-size: 1.6rem;
    font-weight: bold;
}

/* ------------ contact (마지막단) -------------- */
.contactMainBox {
    height: 100%;
    padding: 60px;
    background: #eeeeee;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.contact_me {
    font-family: 'TTHakgyoansimJiugaeR';
    font-size: 3rem;
    font-weight: 700;
    color: black;
}

.contactContainer {
    font-family: 'GmarketSansMedium';
    font-weight: 400;
    color: white;
    text-shadow: 1px 1px 2px #000000; /* X축 이동, Y축 이동, 흐림 정도, 색상 */
    width: 800px;
    height: 100%;
    padding: 20px;
    background: gray;
    border-radius: 30px;
    margin-top: 40px;
}

/* ----------------- 모달창 스타일 ----------------- */
.modal-content {
    max-width: 90% !important;
    font-family: 'GmarketSansMedium';
}
.project_modal_header {
    text-align: center;
    background: black;
    font-size: 3rem !important;
    color: white;
}
.team {
    width: 50%;
}


/* ----------------- 반응형 ----------------- */
@media (max-width: 1200px) {
    /* ---- 큰 태블릿 사이즈 ---- */
    .logo {
        font-size: 2.5rem;
    }

    .navbarButton {
        font-size: 1.2rem;
    }

    .introContainer3 {
        font-size: 3rem;
    }

    .about_me_Container, .content_aboutme {
        font-size: 2rem;
    }

    .skills-cards-container {
        flex-wrap: wrap;
    }

    .projectMainContent {
        flex-direction: column;
        align-items: center;
    }
    .projectMain {
        width: 100%;
    }
    .projectImg, .projectContent {
        width: 100%;
        margin: 0 auto;
    }
    .about_Me {
        font-size: 2.5rem;
    }
    .team {
        width: 50%;
    }


}

