}

@media (max-width: 450px) {
    /* ---- 스마트폰 세로모드 ---- */
    .primaryProjectContent {
        font-size: 2rem;
    }

    .projectContent {
        font-size: 0.8rem;
    }

    .toyProjectContent {
        font-size: 2rem;
    }

    .portfolioProjectContent {
        font-size: 2rem;
    }
    .introContent {
        font-size: 0.9rem;
    }

    .skillsTypeBox, .projectMain {
        width: 90vw;
    }

    .repositoryGithub, .repositoryBlog {
        width: 90vw;
        height: 100%;
    }
    .projectMainContent {
        flex-direction: column;
    }

    .projectContent table {
        width: 100%;
        table-layout: fixed;
    }

    .projectContent table tr {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }

    .projectContent table tr td {
        flex-basis: 100%;
        display: flex;
        justify-content: space-between;
        padding: 0.5rem 0;
    }

    .projectContent table tr td:first-child {
        text-align: left;
    }

    .projectContent table tr td:last-child {
        text-align: right;
    }
    .team {
        font-size: 100%;
        width: 100%;
    }

    .introContainer3 {
        /* 450px 이하일 때의 스타일 */
        text-align: left;
        font-size: 1.3rem;
    }

    .intro_main_text1 {
        font-size: 1.7rem;
    }
    .intro_main_text2 {
        font-size: 1.5rem;
        text-align: center;
    }
    .highlighted-name {
        font-size: 2rem;
    }
    .intro_main_text3 {
        font-size: 0.8rem;
    }

    .resumebtn {
        width: 167px;
        font-size: 90%;
    }

    .project_modal_header {
        font-size: 2rem !important;
    }
    .projectName {
        font-size: 2rem;
    }
    .project_modal_body {
        font-size: 1.3rem;
    }

    .team table {
        width: 100%;
    }
    .team table, .team table tr, .team table td {
        display: block;
    }
    .team table tr {
        margin-bottom: 10px;
    }
    .team table td {
        /* 테이블 데이터를 한 줄로 표시하기 위해 필요한 스타일 */
        width: 100%;
        box-sizing: border-box; /* 패딩과 보더가 너비에 포함되도록 */
        border-radius: 20px;
    }
    .team table td:first-child {
        background: #f0f0f0;

    }
}

@media (max-width: 407px) {
    .highlighted-self, .highlighted-self2 {
        font-size: 1.2rem;
    }
}

@media (max-width: 376px) {
    /* ---- 작은 스마트폰 ---- */
    .introContent {
        font-size: 0.8rem;
    }

    .introContainer3 {
